@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;900&family=Varela+Round&display=swap');

*{
	margin: 0%;
	padding: 0%;
	font-family: "roboto", sans-serif;
}
nav{
  background-color: rgb(2, 11, 25);
  color: white;
  padding: 20px;
  text-align: center;
}
body{
  background-color:rgb(226, 225, 224);
}
.container{
  background-color: rgb(114, 113, 111);
  margin: auto;
  min-height: 100vh;
  width: 70%;
  padding: 4vmax 8vmax;
  box-sizing: border-box;
}
.container > form {
  display: flex;
  flex-direction: column;
}
.container > h1{
  text-align: center;
  margin: 10px ;
  font-size: 2.5rem;
}


input,textarea,button{
  padding: 20px;
  cursor: pointer;
  border: 1px solid black;
  border-radius: 10px;
  margin: 5px 0;
}

button{
  border: none;
  cursor: pointer;
  background-color: black;
  color: aliceblue;
  font-size: 1.5rem;
  
}

/* *{
  border: 1px solid red;
} */
                                       /* coz of flex it come in row */
.task{
  display: flex; 
  background-color: aliceblue; 
  border-radius: 10px;
  padding: 20px;
  align-items: center;
  margin-top: 10px;
}
/* inside task the div become 100% */
.task > div{
  width: 100%;
}

.task span{
  color: rgb(70, 66, 66);
}

.task > button{
  border-radius: 100%;
  width: 30px;
  height: 30px;
  align-items: center;
  justify-content: center;
  display: flex;
  font-size: 2.6rem;
  
}
.task button:hover{
  background-color: red;
}